<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="cancel" 
      :width="800"
    >
      <div slot="title">ozon订单称重检查</div>

      <div class="modal-body">

        <a-row :gutter="16">
          <a-col :span="24" style="width: 400px; margin-bottom: 12px;">
            <a-input ref="package_sku" v-model="package_sku" placeholder="麻袋sku输入.." allowClear/>
          </a-col>

          <a-col :span="24" style="width: 100px; margin-bottom: 12px; margin-right: 20px;">
            <a-button type="primary" icon="edit" @click="addPackageWithPacker">新增麻袋</a-button>
          </a-col>

          <a-col :span="24" style="width: 100px; margin-bottom: 12px; margin-right: 20px;">
            <a-button type="primary" icon="download" @click="printPackageWithPacker">打印麻袋</a-button>
          </a-col>
        </a-row>

        <a-row :gutter="16">
          <a-col :span="24" style="width: 600px; margin-bottom: 12px;">
            <a-input ref="tracking_number" v-model="form.tracking_number" placeholder="ozon订单 运单号输入.." allowClear @pressEnter="toWeight" />
          </a-col>

          <a-col :span="24" style="width: 600px; margin-bottom: 12px;">
            <a-input ref="weight" v-model="form.weight" placeholder="重量输入.." allowClear @pressEnter="confirm" />
          </a-col>

          <a-col :span="24" style="width: 100px; margin-bottom: 12px;">
            <a-button type="primary" icon="search" @click="confirm">确定</a-button>
          </a-col>
        </a-row>

        <a-row style="margin-bottom: 12px; display: flex; align-items: center; border: 1px dotted black; padding: 10px">
          <span class="label"> 检查结果：</span>
          <span class="value"> {{ form.value }}</span>
          <a-button v-if="form.value == '检查通过！'" type="primary" style="margin-left: 60px;" @click="uploadTheWeight"> 重量上传 </a-button>
          <a-button v-if="form.value == '未通过！'" type="primary" style="margin-left: 60px;" @click="labelTheOverWeightOzonOrderToOperation"> 通知运营 </a-button>
        </a-row>

        <a-row style="margin-bottom: 12px; display: flex; align-items: center; border: 1px dotted black; padding: 10px">
          <span class="label"> 原因分析：</span>
          <span class="reason"> {{ form.reason }}</span>
        </a-row>
      </div>
    </a-modal>
  </div>
</template>

<script>
import { checkOzonOrderWeight,labelTheOverWeightOzonOrder,addPackage,printPackage,uploadTheWeight } from '@/api/packer'
import { message } from "ant-design-vue";

export default {
  name: 'FormModal',
  props: ['visible', 'form'],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
      iframe: false,
      package_sku:"",
    };
  },
  computed: {
  },
  watch: {
    form:{
      handler(newValue){
        this.$nextTick(() => {
          this.$refs.purchase_product_id.focus();
        });
      },
      deep:true
    }

  },
  methods: {
    toWeight(){
      this.$nextTick(() => {
        this.$refs.weight.focus();
      });
    },
    confirm() {
      this.loading = true;
      
      checkOzonOrderWeight({tracking_number:this.form.tracking_number,weight:this.form.weight}).then(
        res => {
          this.form.reason = res.reason
          this.form.value = res.value
          message.success(res.msg)
        }
      ).finally(() => {
        this.loading = false;
      })
    },
    labelTheOverWeightOzonOrderToOperation(){
      labelTheOverWeightOzonOrder(this.form).then(
        res => {
          message.success(res.msg)
          this.form = {}
        }
      )
    },
    uploadTheWeightInOzonOrder(){
      uploadTheWeight({...this.form,package_sku:this.package_sku}).then(
        res => {
          message.success(res.msg)
          this.form = {}
        }
      )
    },
    cancel() {
      this.$emit('update');
    },
    addPackageWithPacker(){
      addPackage().then(
        res => {
          this.package_sku = res.sku
          message.success(res.msg)
        }
      )
    },
    printPackageWithPacker(){
      printPackage({package_sku: this.package_sku}).then(
        res => {
          // 触发打印
          const pdfBase64 = res.data;
          const pdfBlob = base64ToBlob(pdfBase64);
          const pdfUrl = URL.createObjectURL(pdfBlob);

          // 创建并加载 iframe
          if(this.iframe == false){
            this.iframe = document.createElement('iframe');
            document.body.appendChild(this.iframe);
          }

          let iframe = this.iframe

          iframe.style.display = 'none'; // 隐藏 iframe
          iframe.src = pdfUrl;

          iframe.onload = () => {
            iframe.contentWindow.focus();
            iframe.contentWindow.print(); // 触发打印
            message.success(res.msg);
          };

          // 将 base64 编码的 PDF 转换为 Blob
          function base64ToBlob(base64) {
            const byteCharacters = atob(base64);
            const byteArrays = [];

            for (let offset = 0; offset < byteCharacters.length; offset += 1024) {
              const slice = byteCharacters.slice(offset, offset + 1024);
              const byteNumbers = new Array(slice.length);
              for (let i = 0; i < slice.length; i++) {
                byteNumbers[i] = slice.charCodeAt(i);
              }
              byteArrays.push(new Uint8Array(byteNumbers));
            }

            return new Blob(byteArrays, { type: 'application/pdf' });
          }
        }
      )
    }
  },
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns {
  display: flex;
  margin-right: 30px;
  align-items: center
}

.label{
  width: 100px;
  margin-right: 5px;;
}

.value{
  margin-right: 5px;
  color: green;
}
</style>